.navbar {
	& .navbar-brand {
		z-index: 99;
	}
	&.navbar-dark {
		&.fixed-top {
			&:before {
				content: "";
				width: 100%;
				height: 60px;
				background: #333;
				top: 0;
				bottom: 0;
				left: 0;
				position: absolute;
				@include Opacity(.3);
			}
		}
		& .navbar-nav {
			& .nav-item {
				&:hover {
					& .nav-link {
						color: #333;
					}
				}
			}
			& .nav-link {
				&:hover {
					color: #333;
				}
			}
		}
	}
	& .navbar-nav {
		flex-direction: row;
	}
	& .nav-item {
		& .nav-link {
			padding: .5rem 1rem;
		}
		&.open {
			& .dropdown-menu {
				display: block;
			}
		}
	}
	@media screen and (min-width: 768px) {
		&.navbar-light {
			.navbar-nav {
				.nav-link {
					color: #333;
					font-weight: 400;
				}
			}
		}
		& .navbar-collapse {
			display: block;
			flex-basis: 10%;
		}
		& .navbar-toggler {
			display: none;
		}
		& .dropdown-menu {
			position: absolute;
			margin-top: 0px;
			padding: 0;
			@include border-radius(0);
			border: 0;
			font-size: 13px;
			@include transition();
			& .dropdown-toggle {
				&::after {
					border-top: 0.3em solid transparent;
					border-right: 0 solid transparent;
					border-bottom: 0.3em solid transparent;
					border-left: 0.3em solid;
					top: 3px;
					position: relative;
				}
			}
			& .dropdown-menu {
				display: none;
				left: 100%;
				top: 0;
				border-left: 1px solid $dropdownBorderColor;
			}
			& .dropdown-item {
				padding: .25rem 1rem;
				border-bottom: 1px solid $dropdownBorderColor;
				&:hover,
				&:focus {
					background-color: $dropdownItemHoverColor;
					color: #333;
				}
			}
		}
		& .navbar-nav {
			& .nav-item {
				&:hover {
					& .nav-link {
						background-color: $navItemBgHoverColor;
					}
					&>.dropdown-menu {
						display: block;
						background-color: $dropdownMenuBgColor;
						& .dropdown {
							&:hover {
								&>.dropdown-menu {
									background-color: $dropdownMenuBgColor;
									display: block;
								}
							}
						}
					}
				}
			}
		}
	}
	@media screen and (max-width: 767px) {
		.navbar-toggler-close {
			display: none;
			color: #fff;
			font-size: 2rem;
		}
		body.menu-open & {
			& .navbar-toggler-close {
				display: inline;
			}
			.navbar-toggler-icon {
				display: none;
			}
		}
		& .navbar-toggler {
			display: block;
			border: 0;
			cursor: pointer;
			&:active,
			&:hover {
				background: transparent;
				outline: 0;
			}
		}
		& .navbar-collapse {
			display: block;
			position: fixed;
			left: -200px;
			top: 0;
			border: 0;
			background: #f1f2f3;
			height: 100%;
			width: 200px;
			z-index: 9999;
			@include transition();
			&.show {
				display: block;
				left: 0;
				& .navbar-nav {
					flex-direction: column;
					& a {
						&:focus,
						&:active {
							background-color: $dropdownItemMobileActiveColor;
							color: #333;
						}
					}
					& .nav-item {
						padding: 0;
						& .nav-link {
							padding: 7px 15px;
							border-bottom: 1px solid #e5e5e5;
						}
					}
					& .dropdown-menu {
						background: transparent;
						border: 0;
						font-size: 13px;
						padding: 0px;
						margin: 0;
						& a {
							border-bottom: 1px solid #e5e5e5;
						}
					}
				}
				& .parent-open>.dropdown-menu {
					display: block;
					float: none;
				}
			}
		}
	}
}